@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
:root {
  --font-family-default: 'Roboto', sans-serif;

  --clr-background-1: 0, 0%, 100%;
  --clr-background-2: 210, 22%, 89%;
  --clr-background-3: 0, 0%, 0%;
  --clr-primary: 209, 100%, 25%; /* dark-blue */
  --clr-secondary: 206, 25%, 80%; /* light-blue */
  --clr-white: 0, 0%, 100%;
  --clr-gold: 37, 100%, 62%;

  --circle-gold: 39, 75%, 47%;
  --circle-turquoise: 198, 64%, 36%;
  --circle-green: 102, 23%, 46%;
  --circle-turquoise: 198, 82%, 29%;
  --circle-green: 100, 39%, 48%;
  --circle-red: 5, 53%, 47%;

  --fs-100: .5rem;
  --fs-200: .875rem;
  --fs-300: 1rem;
  --fs-400: 1.125rem;
  --fs-500: 1.25rem;
  --fs-600: 1.5rem;
  --fs-700: 1.75rem;
  --fs-800: 2.5rem;
  --fs-900: 6rem;

  --carousel-width: 400px;
  --carousel-height: 300px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  scroll-behavior: smooth;
}

body { 
  position: relative;
  overscroll-behavior: none;
  background-color: hsl(var(--clr-white));
  color: hsl(var(--clr-primary));
  font-family: var(--font-family-default);
  font-size: var(--fs-400);
  font-weight: 400;
}

section {
  position: relative;
  width: 100%;
  padding: 2rem;
}

.section-header {
  margin-bottom: 2rem;
  font-size: var(--fs-800);
  font-weight: 600;
  min-width: 100px;
}

.section-subhead {
  margin-top: 2rem;
  margin-bottom: 1rem;
  font-size: var(--fs-700);
  font-weight: 600;
  max-width: 700px;
}

.back-btn {
  position: relative;
  display: flex;
  align-items: center;
  align-self: flex-start;
  position: fixed;
  top: 10.4rem;
  left: 1rem;
  cursor: pointer;
  z-index: 20;
}

.back-btn-icon {
  font-size: 1.7rem;
}

.single-page-layout {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding: 10rem 17rem;
}

.single-page-content {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 1400px;
}

.single-page-header {
  align-self: flex-start;
}

.copy {
  line-height: 1.3;
  max-width: 900px;
  margin: 0 0 1rem 0 ;
}

.block {
  font-size: 1.5rem;
  font-weight: 500;
  font-style: italic;
  padding: 1.5rem;
  color: hsl(var(--clr-white));
  border-radius: .4rem;
  margin: 1rem 0 2rem;
  box-shadow: 6px 6px 10px hsl(var(--clr-primary), .4);
}

.btn-hover:hover {
  transform: scale(.98);
  box-shadow: inset 3px 3px 10px hsl(var(--clr-background-3), .4);
}

.red-background {
  background-color: hsl(var(--circle-red), 1);
}

.yellow-background {
  background-color: hsl(var(--circle-gold), 1);
}

.turquoise-background {
  background-color: hsl(var(--circle-turquoise), .8);
}

.green-background {
  background-color: hsl(var(--circle-green), .8);
}

.navy-background {
  background-color: hsl(var(--clr-primary), .8);
}

.burgundy-background {
  background-color: #861657;
}

.brick-background {
  background-color: #a64253;
}

.pink-background {
  background-color: #d56aa0;
}

.red-border {
  border: 2px solid hsl(var(--circle-red), .8);
  color: hsl(var(--circle-red), .8);
  /* box-shadow: 6px 6px 10px hsl(var(--circle-red), .4); */
}

.yellow-border {
  border: 2px solid hsl(var(--circle-gold), .8);
  color: hsl(var(--circle-gold), .8);
  /* box-shadow: 6px 6px 10px hsl(var(--circle-gold), .4); */
}

.green-border {
  border: 2px solid hsl(var(--circle-green), 9);
  color: hsl(var(--circle-green), .9);
  /* box-shadow: 6px 6px 10px hsl(var(--circle-green), .4); */
}

.navy-border {
  border: 2px solid hsl(var(--clr-primary), .8);
  color: hsl(var(--clr-primary), .8);
  /* box-shadow: 6px 6px 10px hsl(var(--clr-primary), .4); */
}

.turquoise-border {
  border: 2px solid hsl(var(--circle-turquoise), .8);
  color: hsl(var(--circle-turquoise), .8);
  /* box-shadow: 6px 6px 10px hsl(var(--circle-turquoise), .4); */
}

.position-left {
  align-self: flex-start;
}

.position-right {
  align-self: flex-end;
}

.position-center {
  align-self: center;
}

.cta-btn {
  background-color: hsl(var(--clr-primary));
  color: hsl(var(--clr-white));
  border: none;
  border-radius: .7rem;
  padding: 1rem 2rem;
  font-size: var(--fs-400);
  font-weight: 300;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.program-btn {
  margin-top: 2rem;
}

.contact-link {
  color: hsl(var(--clr-primary));
  text-decoration: underline;
  text-underline-offset: .2rem;
}

.contact-link:hover {
  color: #861657;
}

.master-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

/* INTRO SECTION */

.intro-section {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: hsl(var(--clr-background-2));
  height: 100vh;
}

.intro-img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}

.intro-header {
  position: absolute;
  top: 25%;
  left: 31%;
  color: hsl(var(--clr-white));
  font-size: 7rem;
  font-weight: 600;
  text-shadow: 4px 4px 6px #b7b7b7;
}

.intro-copy-container {
  position: absolute;
  top: 66%;
  left: 45%;
  display: flex;
  align-items: center;
  width: 50%;
}

.intro-line-graphic {
  background-color: hsl(var(--clr-primary));
  height: 2px;
  width: 30rem;
}

.intro-copy {
  margin-left: 3rem;
  line-height: 1.2;
  max-width: 24rem;
}

.intro-cta {
  position: absolute;
  left: 5%;
  bottom: 5%;
  font-size: 1.3rem;
  z-index: 2;
}

/* QUOTE BANNER */

.quote-banner {
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(100% - 12rem);
  padding: 2rem 0rem;
  margin-top: 4rem;
  font-size: 1.5rem;
  font-weight: 300;
  text-align: center;
  line-height: 1.3;
  color: hsl(var(--clr-white));
  border-top: 2px solid #a64253;
  border-bottom: 2px solid #a64253;
}

.quote-banner span {
  font-size: 2.7rem;
  font-weight: 500;
  line-height: .5;
  font-style: italic;
}

.quote-header {
  max-width: 860px;
  padding: 2rem 1.5rem;
  background-color: #a64253;
  border-radius: .5rem;
}

/* APPROACH SECTION */

.approach-section {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 8rem 0;
}

.approach-content {
  width: calc(100% - 12rem);
  height: 100%;
}

.approach-graphic {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}

.approach-team {
  position: relative;
  margin: 3rem 1rem;
}

.approach-background {
  display: flex;
  justify-content: center;
  max-width: 380px;
  align-items: center;
  height: 250px;
  border-radius: .5rem;
  color: hsl(var(--clr-white));
  box-shadow: 2px 2px 15px #5a5858b5;
}

.approach-copy.highlight {
  padding: 1rem;
  margin: 3rem 0 7rem;
  font-size: 1.2rem;
  font-weight: 500;
  text-align: center;
  border-top: 2px solid hsl(var(--clr-primary));
  border-bottom: 2px solid hsl(var(--clr-primary));
}

.approach-content-container {
  display: flex;
  width: 100%;
}

.approach-content-bottom {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: calc(100% - 12rem);
  height: 100%;
  background-color: #861657;
  color: hsl(var(--clr-white));
  padding: 3rem 2rem 4rem;
  border-radius: .5rem;
}

.approach-copy-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  margin-left: 2rem;
}

.approach-copy-container .copy {
  max-width: 600px;
}

.approach-copy.last {
  margin-bottom: 2rem;
}

.approach-family-img {
  width: 40%;
  height: auto;
  object-fit: cover;
  border-radius: .5rem;
}

.approach-copy-quote {
  background-color: hsl(var(--clr-white));
  color: #861657;
  padding: 1rem 2rem 2rem;
  font-size: 1.4rem;
  border-radius: .5rem;
  margin-top: auto;
  margin-bottom: 0;
}

.quote-left {
  font-size: 2.2rem;
  margin-right: .4rem;
}

.quote-right {
  position: absolute;
  font-size: 2.2rem;
  margin-top: .4rem;
  margin-left: .4rem;
}

.team-img {
  height: 300px;
  z-index: 10;
}

.team-copy {
  max-width: 330px;
  font-size: 1.7rem;
  padding: 1rem;
  z-index: 9;
}






/* PROGRAMS HOME SECTION */

.programs-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 4rem;
  margin-bottom: 6rem;
  background-color: hsl(var(--clr-gold), .3);
}

.programs-intro {
  text-align: center;
}

.programs-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1400px;
  margin: 3rem 2rem;
}

.program-container {
  display: flex;
  margin-bottom: 2rem;
}

.program-container:nth-child(odd) {
  flex-direction: row-reverse;
}

.programs-img {
  width: 400px;
  height: 300px;
  object-fit: cover;
  border-radius: .5rem;
  float: left;
  margin-right: 2rem;
  margin-bottom: .5rem;
}

.programs-img-1,
.programs-img-3,
.programs-img-5 {
  border-top-right-radius: 20% 50%;
  border-bottom-right-radius: 20% 50%;
  object-position: 100% 0;
}

.programs-img-2,
.programs-img-4 {
  border-top-left-radius: 20% 50%;
  border-bottom-left-radius: 20% 50%;
  object-position: 100%;
}

.program-1 .programs-copy,
.program-3 .programs-copy,
.program-5 .programs-copy {
  padding: 1rem 1.5rem 2rem 4rem;
}

.program-2 .programs-copy,
.program-4 .programs-copy {
  padding: 1rem 4rem 2rem 1.5rem;
}

.program-1,
.program-3,
.program-5,
.program-1 .programs-subhead,
.program-3 .programs-subhead,
.program-5 .programs-subhead {
  text-align: right;
}

.programs-img-1 {
  object-position: 75%;
}

.programs-img-2,
.programs-img-5 {
  object-position: 50%;
}

.programs-img-4 {
  object-position: 100%;
}

.programs-subhead {
  font-weight: 600;
  font-size: 1.25rem;
  text-align: left;
  margin-bottom: 1rem;
}

.programs-copy {  
  max-width: 35rem;
  transition: all .3s ease-in-out;
  background-color: hsl(var(--clr-white), .7);
}

.programs-copy.copy {
  margin-bottom: 0;
}

.program-1 {
  align-self: flex-start;
}

.program-2,
.program-5  {
  align-self: flex-end;
}

.program-3 {
  align-self: center;
}

/* RIGHTS HOME BANNER */

.rights-banner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100% - 12rem);
  padding: 1rem;
  font-size: 2rem;
  border-top: 2px solid #861657;
  border-bottom: 2px solid #861657;
  color: #861657;
}

.rights-banner > * {
  margin: 0 4rem;
}

.rights-header {
  color: #861657;
  font-weight: 600;
}

.rights-cta {
  background-color: #861657;
  color: hsl(var(--clr-white));
  border: none;
  border-radius: .7rem;
  padding: 1rem 2rem;
  font-size: 1.8rem;
  font-weight: 300;
  cursor: pointer;
}

/* NEWS HOMEPAGE SECTION */

.news-section {
  display: relative;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 6rem;
}


.news-carousel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5rem 0 7rem;
  overflow: hidden;
}

.carousel-arrow {
  font-size: 2.5rem;
}

.carousel-item-container {
  display: flex;
  flex-direction: column;
  width: 20vw;
  height: 550px;
  text-align: left;
  font-size: 1rem;
}

.carousel-item-container > * {
  margin-bottom: 1rem;
  color: hsl(var(--clr-primary));
}

.carousel-item-img {
  width: 100%;
  height: 20vw;
  object-fit: cover;
  border-radius: .8rem;
}

.carousel-item-header {
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 1.1;
  margin: 1rem 0 ;
}

.carousel-item-summary {
  line-height: 1.3;
}

.carousel-item-readmore {
  display: flex;
  margin-top: auto;
  margin-bottom: 0;
  text-align: right;
  justify-content: flex-end;
}

.carousel-item-readmore p {
  margin-right: .5rem;
}


.carousel-item-readmore:hover {
  text-decoration: underline;
  text-underline-offset: 3px;
}

.news-footer {
  display: flex;
  justify-content: space-between;
  margin-top: auto;
  padding: 0 6rem;
}

.news-social-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 350px;
}

.news-social-copy {
  font-size: 1.1rem;
  font-weight: 300;
}

.news-social-icons {
  font-size: 1.6rem;
  color: hsl(var(--clr-primary));
  margin-left: .4rem;
}

.news-viewall-btn {
  background-color: hsl(var(--clr-primary));
  padding: .6rem 1rem;
  border: none;
  border-radius: .5rem;
  font-size: 1rem;
  color: hsl(var(--clr-white));
}

/* NEWS ALL ARTICLES PAGE*/

.article-container {
  display: flex;
  width: 100%;
  height: 200px;
  border-radius: .4rem;
  margin-bottom: 1.5rem;
}

.article-container .article-img {
  width: 200px;
  height: 200px;
  object-fit: cover;
}

.article-copy {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 500px;
  margin-left: 1rem;
  color: hsl(var(--clr-primary));
}


/* NEWS INDIVIDUAL ARTICLE PAGE*/

.article-detail-copy {
  max-width: 70%;
}

.article-detail-copy img {
  height: 200px;
  width: 200px;
  object-fit: cover;
}

/* CONTACT SECTION */
.contact-section {
  position: relative;
  background-color: hsl(var(--clr-background-2));
  color: hsl(var(--clr-primary));
  padding: 6rem 10rem;
}

.contact-content {
  position: relative;
  display: flex;
  justify-content: space-around;
  background-color: hsl(var(--clr-secondary));
  border-radius: .7rem;
}

.contact-content-left,
.contact-content-right {
  width: 45%;
  padding: 3rem 2rem;
}

.contact-map-img {
  width: 100%;
  margin: 1.5rem 0 2rem;
  border-radius: .7rem;
}

.contact-copy {
  margin-bottom: 1rem;
}

.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: .4rem;
  margin-top: 2rem;
  font-weight: 400;
}

.contact-form input,
.contact-form textarea {
  font-family: var(--font-family-default);
  font-size: var(--fs-400);
  line-height: 1.2;
  color: hsl(var(--clr-primary));
}

.contact-form textarea {
  padding: 1rem;
}

.select-label {
  display: flex;
  flex-direction: column;
}

.contact-form select {
  font-family: var(--font-family-default);
  font-size: 1.1rem;
  color: hsl(var(--clr-primary));
  margin-top: 1rem;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
}

.contact-form select::placeholder,
.contact-form input::placeholder,
.contact-form textarea::placeholder {
  font-family: var(--font-family-default);
  font-size: var(--fs-400);
  line-height: 1.2;
  color: hsl(var(--clr-primary));
  font-weight: 400;
}

.contact-form input:focus, 
.contact-form select:focus, 
.contact-form textarea:focus {
  outline: none;
} 

.contact-form input, 
.contact-form select {
  min-width: 100%;
  background-color: hsl(var(--clr-white));
  padding: .7rem 1rem;
  margin-bottom: 1.5rem;
  border: none;
  border-radius: .3rem;
}

.contact-form input.input-error,
.contact-form select.input-error {
  border: 1.5px solid #e10000;
  border-radius: .5rem;
}

.contact-form textarea {
  min-width: 100%;
  max-width: 100%;
  height: 227px;
  padding-top: .5rem;
  margin: 0 0 2.5rem;
  background-color: hsl(var(--clr-white));
  border: none;
  border-radius: .4rem;
  resize: none;
}

.contact-form textarea.input-error {
  border: 1.5px solid #e10000;
}

.contact-btn {
  align-self: flex-start;
  background-color: hsl(var(--clr-white));
  color: hsl(var(--clr-primary));
  border: none;
  border-radius: .7rem;
  padding: 1rem 2rem;
  font-size: 1.1rem;
  cursor: pointer;
}

.success-popup {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 40rem;
  padding: 2rem;
  background-color: hsl(var(--clr-accent), .4);
  border-radius: .4rem;
  color:hsl(var(--clr-white));
  text-align: center;
  opacity: 0;
  top: -200px;
  transform: translate(-50%, -50%) scale(0.5);
  transition: opacity 300ms ease-in-out,
              top 500ms ease-in-out,
              transform 500ms ease-in-out;
}

.checkmark-background {
  position: relative;
  width: 3rem;
  height: 3rem;
  background-color: #fff;
  border-radius: 50%;
}

.success-checkmark {
  position: absolute;
  top: 0;
  left: 0;
  font-size: var(--fs-900);
  color: hsl(var(--clr-accent));
}

.success-message {
  color: hsl(var(--clr-accent));
}

.success-thanks {
  margin-bottom: .8rem;
}

.success-btn {
  margin-top: 3.5rem;
}

/* FOOTER */


.footer {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: auto;
  width: 100%;
  background-color: #5a5858;
  padding: 3rem 6rem 1rem;
}

.footer-content {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-bottom: 4rem;
  z-index: 3;
}

.footer-content {
  width: 80%;
}

.footer-category {
  margin-bottom: .6rem;
  color: hsl(var(--clr-white));
  font-size: .9rem;
  font-weight: 200;
}

.footer-content ul > a {
  margin-bottom: .6rem;
  color: hsl(var(--clr-white));
  font-size: .9rem;
  font-weight: 200;
  margin-left: 1rem;
}

.footer-left,
.footer-right {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.footer-left-top,
.footer-left-middle,
.footer-left-bottom,
.footer-right-top,
.footer-right-middle {
  display: flex;
  flex-direction: column;
}

.footer-right-bottom {
  display: flex;
  justify-content: right;
}

.footer-right-bottom a img {
  height: 20px;
  width: 20px;
  margin: 5px 0 0 10px;
}

.footer-right {
  text-align: right;
}

.footer a {
  transition: all .1s ease-in-out;
}

.footer a:hover {
  font-weight: 300;
}

.footer-social-icons {
  font-size: 1.5rem;
  color: hsl(var(--clr-white));
  margin-left: .4rem;
  transition: all .2s ease-in-out;
}

.footer-social-icons:hover {
  scale: 1.1;
}

.copyright {
  color: hsl(var(--clr-white));
  font-size: .9rem;
  font-weight: 200;
}

/* STAFF PAGE */
.staff-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.staff-page-layout {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10rem 5rem;
}

.staff-main-header {
  text-align: center;
}

.staff-main-header {
  align-self: center;
  max-width: 700px;
}

.staff-copy {
  max-width: 70%;
  line-height: 1.2;
  align-self: center;
}

.offices-container {
  position: relative;
  width: 95%;
  border: 2.5px solid hsl(var(--clr-primary));
  margin: 3rem 0 ;
  background-color: hsl(var(--clr-white));
  border-radius: .5rem;
  z-index: 10;
  box-shadow: 10px 10px 30px hsl(var(--clr-primary), .2);
}

.office-content {
  animation: growDown 500ms ease-in-out;
  animation-fill-mode: forwards;
  transform-origin: top center;
  line-height: 1;
  margin: 0 1rem;
}

@keyframes growDown {
  0% {
      transform: scaleY(0)
  }
  80% {
      transform: scaleY(1.1)
  }
  100% {
      transform: scaleY(1)
  }
}

.no-dropdown {
  display: flex;
  align-items: center;
  height: 50px;
  background-color: hsl(var(--clr-primary));
  color: hsl(var(--clr-white));
}

.no-dropdown .office-header {
  font-weight: 400;
}

.office {
  padding: .5rem 1rem;
  border-bottom: 2.5px solid hsl(var(--clr-primary));
  width: 100%;
  font-weight: 300;
}

.office-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.office:last-child {
  border-bottom: none;
}

.office-name {
  font-size: 1.4rem;
}

.office-address {
  max-width: 280px;
}

.office-phone {
  margin: .5rem 0;
}


.staff-profiles .empty-office-message {
  padding: 3rem 0 4rem;
  font-size: 1.5rem;
}

.staff-profiles {
  display: flex;
  flex-wrap: wrap;
  min-height: 150px;
  max-height: 650px;
  overflow-x: scroll;
  margin: 2rem 0;
}

.staff-profile {
  display: flex;
  flex: 1 300px;
  height: 100%;
  max-height: 270px;
  padding: .5rem;
  margin: 0 1rem 1rem 0;
}

.staff-info-container {
  display: flex;
  flex-direction: column;
  min-width: 250px;
  height: 250px;
  padding: 0 1.5rem;
}

.staff-img {
  width: calc(725px / 3.5);
  height: 100%;
  object-fit: cover;
  border-radius: .8rem;
}

.staff-firstname,
.staff-lastname {
  font-size: 1.8rem;
}

.staff-title {
  max-width: 10rem;
  margin: .5rem 0;
}

.staff-email, 
.staff-phone {
  display: flex;
  align-items: center;
}

.staff-email {
  font-size: .92rem;
  margin-top: auto;
  cursor: pointer;
}

.staff-email.small {
  font-size: .78rem;
  letter-spacing: -.5px;
}

.small .staff-email-icon {
  font-size: .92rem;
}

.staff-email-icon, 
.staff-phone-icon {
  margin-right: .7rem;
}

.staff-phone {
  margin-top: .5rem;
}

.staff-bio {
  padding-right: 1rem;
  overflow: scroll;
  line-height: 1.2;
  min-width: 300px;
  max-width: 700px;
}

.toggle-office-icon {
  font-size: 2.8rem;
  padding: .5rem;
  transform: rotate(0deg);
  transition: transform .3s linear;
}

.toggle-office-icon.open{
  transform: rotate(180deg);
  transition: transform .3s linear;
}

.show-bio-btn {
  padding: .3rem;
  margin-top: .5rem;
  background-color: hsl(var(--clr-primary));
  font-size: .9rem;
  text-align: center;
  color: hsl(var(--clr-white));
  border-radius: .3rem;
  cursor: pointer;
  transition: all .2s ease-in-out;
}

/* ABOUT & MISSION PAGE */
.about-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  width: 100%;
  height: 100%;
}

.about-content-bottom {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 7rem;
}

.about-logo {
  width: 250px;
  margin-right: 2rem;
}

.about-graphic-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-circle-graphic {
  width: 600px;
  padding-bottom: 4rem;
}

@keyframes up-right {
  0% {
      transform: scale(1);
      opacity: .25
  }
  50% {
      transform: scale (1, 5);
      opacity: 1;
  }
  100% {
      transform: scale(1);
      opacity: .25;
  }
}
.circle {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  opacity: .25;
  cursor: pointer;
}

.circle-1 {
  position: absolute;
  top: 12rem;
  left: 5rem;
}

.circle-2 {
  position: absolute;
  top: 12rem;
  right: 5rem;
}

.circle-3 {
  position: absolute;
  bottom: 7.5rem;
}

.white {
  background-color: hsl(var(--clr-white));
  -webkit-animation: up-right 1s infinite;
  -moz-animation: up-right 1.5s infinite;
  -o-animation: up-right 1.5s infinite;
  animation: up-right 1.5s infinite;
}

.graphic-prongs {
  position: absolute;
  top: 4rem;
  left: -14rem;
  width: 300px;
  padding: 1rem;
  border: 1px solid #861657;
  background-color: hsl(var(--clr-white));
  text-align: center;
  font-size: 1.4rem;
}

.hidden {
  display: none;
}

.holistic-copy {
  position: absolute;
  bottom: 17rem;
  width: 300px;
  font-size: 1.2rem;
  font-weight: 500;
  text-align: center;
}

.mission-graphic-container {
  display: flex;
  justify-content: space-between;
  margin-top: 3rem;
}

.mission-step {
  padding: 2rem;
  border: 1px solid hsl(var(--clr-primary));
  margin: 0 1rem;
  width: 300px;
}

.mission-header {
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 500;
}

.mission-step-graphic {
  margin: 1rem 0;
  border: 1px solid #861657;
}

/* Family Defense */

.family-defense-intro {
  max-width: 1000px;
  margin-bottom: 4rem;
}

.courthouse-img {
  width: 55%;
  min-width: 500px;
  border-radius: .5rem;
  float: left;
  margin-right: 2rem;
}

.prevention-intro {
  max-width: 1000px;
}

@media screen and (max-width:1250px) {

  .offices-container {
    width: 100%;
  }

  .office {
    width: 100%;
    padding: .5rem;
  }

  .office-name {
    font-size: 1.2rem;
    max-width: 80%;
  }

  .staff-profile {
    width: 100%;
    max-height: 100%;
    align-self: center;
    justify-content: space-around;
  }

  .staff-info-container {
    display: flex;
    flex-direction: column;
    min-width: 250px;
    height: 350px;
    padding: 0 1.5rem;
  }

  .staff-img {
    width: calc(725px / 2.5);
    height: 100%;
  }
}

@media screen and (max-width: 1100px) {

  .approach-section {
    padding: 2rem;
  }

  .approach-content-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .approach-content {
    width: 100%;
  }

  .approach-content-bottom {
    width: 100%;
  }

  .approach-copy-container {
    margin-left: 0rem;
  }

  .approach-family-img {
    width: 100%;
    max-width: 600px;
    margin-bottom: 1rem;
  }
}


@media screen and (max-width: 800px) {
  section {
    padding: 0rem;
  }

  .back-btn {
   position: absolute;
   top: auto;
   bottom: 2rem;
  }

  .intro-section,
  .approach-section,
  .contact-section,
  .news-section,
  .staff-section {
    padding: 2rem;
  }

  .single-page-layout {
    padding: 6rem 2rem;
  }

  .copy {
    max-width: 100%;
  }

  .intro-img {
    width: 100%;
    object-fit: cover;
  }

  .intro-header {
    left: 0;
    font-size: 4rem;
    text-shadow: 4px 4px 9px #5a5858;
    margin-left: 2rem;
  }

  .intro-copy-container {
    color: hsl(var(--clr-white));
    text-shadow: 4px 4px 9px #5a5858;
    top: 50%;
    left: 0;
    display: flex;
    align-items: center;
    width: 100%;
  }

  .intro-line-graphic {
    display: none;
  }

  .intro-copy {
    margin-left: 2rem;
    max-width: 24rem;
  }

  .quote-banner {
    width: 90%;
    padding: 2rem 1rem;
    margin-top: 3rem;
  }

  .programs-section {
    padding: 2rem;
  }

  .program-container {
    flex-direction: column;
  }

  .program-container:nth-child(odd) {
    flex-direction: column;
  }

  .programs-img-1,
  .programs-img-2,
  .programs-img-3,
  .programs-img-4,
  .programs-img-5 {
    width: 100%;
    border-radius: 0;
  }

  .program-1 .programs-copy,
  .program-2 .programs-copy,
  .program-3 .programs-copy,
  .program-4 .programs-copy
  .program-5 .programs-copy {
    padding: 1rem;
  }

  .rights-banner {
    flex-direction: column;
    font-size: 1.3rem;
    white-space:nowrap;
  }

  .rights-banner > * {
    margin: .3rem 0;
  }

  .rights-cta {
    padding: 1rem;
    font-size: 1.2rem;
    font-weight: 300;
    cursor: pointer;
  }

  .news-carousel {
    margin: 2rem 0;
    justify-content: center;
  }

  .carousel-arrow {
    font-size: 2rem;
  }

  .carousel-item-container {
    width: 100%;
    text-align: left;
    font-size: 1rem;
    margin: 0 2rem
  }

  .carousel-item-img {
    height: auto;
  }

  .news-footer {
    padding: 0rem;
  }

  .news-social-links {
    justify-content: center;
    width: 100%;
  }

  .news-social-copy {
    max-width: 50%;
  }

  .contact-content {
    flex-direction: column;
  }

  .contact-content-left,
  .contact-content-right {
    width: 100%;
    padding: 1rem 2rem;
  }

  .footer-content {
    flex-direction: column;
  }

  .footer-content > * {
    align-self: center;
  }

  .footer-content .footer-right a {
    margin-left: 0;
  }

  .footer-right {
    text-align: left;
  }

  .footer-right > * {
    margin: 0;
  }

  .footer-right-bottom {
    justify-content: center;
  }

  .staff-page-layout {
    margin-top: 6rem;
  }

  .staff-profile {
    flex-direction: column;
  }

  .staff-copy {
    text-align: left;
  }

  /* .staff-profiles {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 2rem 1rem;
    margin: 2rem 0;
  } */
}



