/* SECTION GRAPHICS PLACEMENT */


.section-graphic {
  position: absolute;
}

.intro-right {
  right: 0;
  top: 0;
  height: 120%;
}

.logo-watermark {
  right: -12rem;
  top: -8rem;
  height: 60%;
  opacity: .8;
}

.intro-bottom {
  bottom: 0;
  height: 95%;
  z-index: 2;
}

.approach-right {
  top: 7rem;
  right: 0;
  height: 40rem;
  z-index: -10;
}

.approach-left {
  bottom: 20rem;
  left: 0;
  height: 45rem;
  z-index: -10;
}

.programs-right {
  right: 1rem;
  bottom: .5rem;
  height: 15rem;
}

.programs-left {
  height: 14rem;
  top: 1rem;
  left: 6rem;
}

.programs-right-bottom {
  bottom: -5rem;
  right: 0rem;
  height: 40rem;
}

.programs-bottom {
  top: 1rem;
  right: 1rem;
  height: 14rem;
}

.news-graphic-home {
  position: absolute;
  bottom: 0;
  right: -10rem;
  width: 100rem;
  opacity: .4;
 z-index: -1;
}

.contact-graphic-left {
  left: 0;
  top: 0;
  height: 80rem;
  z-index: 0;
}

.contact-graphic-right {
  right: 0;
  top: 0;
  height: 80rem;
  z-index: 0;
}

.footer-graphic-left {
  bottom: 0;
  left: 0;
  height: 130%;
  z-index: 0;
}

.footer-graphic-right {
  bottom: 0;
  right: 0;
  height: 50%;
  z-index: 0;
}


/* SINGLE PAGE GRAPHICS PLACEMENT */

.single-page-graphic {
  position: absolute;
  z-index: -10;
}

.legal-graphic-left {
  top: 15rem;
  left: 0;
  height: 35vw;
}

.legal-graphic-right {
  right: -10rem;
  bottom: 0;
  height: 30vw;
}

.staff-graphic-bottom {
  bottom: 0rem;
  left: -5rem;
  height: 35rem;
}

.staff-graphic_right {
  right: -18rem;
  height: 27rem;
}

@media screen and (max-width: 800px) {

  .contact-graphic-left {
    left: 0;
    top: 30%;
    height: 90rem;
    z-index: 0;
  }
  
  .contact-graphic-right {
    right: -20rem;
    top: -2rem;
    height: 80rem;
    z-index: 0;
  }
}